import React from 'react';
import styled, { keyframes } from 'styled-components';

const Blooddrop = keyframes`
  0% { opacity: 0; }
  100% { transform: translateY(2.5em) rotate(-45deg); opacity: 1;}
`;

const Mouse = styled.div`
  height: 2.7em;
  width: 6.3em;
  background: black;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 1em 1em 50% 50%;
`;
const ToothLeft = styled.div`
  position: absolute;
  left: 1.35em;
  width: 0;
  height: 0;
  border-left: 0.75em solid transparent;
  border-right: 0.75em solid transparent;
  border-top: 1.5em solid white;
  float: left;
`;
const ToothRight = styled.div`
  position: absolute;
  left: 2.43em;
  width: 0;
  height: 0;
  border-left: 0.75em solid transparent;
  border-right: 0.75em solid transparent;
  border-top: 1.5em solid white;
  float: left;
  margin-left: 1em;
`;
const Blood = styled.div`
  position: absolute;
  top: 0.9em;
  right: 6.75em;
  width: 1.62em;
  height: 1.62em;
  border: 0 solid rgba(0, 0, 0, 1);
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  transform: rotate(-45deg);
  background: #f53057;
  animation: ${Blooddrop} 1.5s infinite;
`;

const App: React.FC = () => {
  return (
    <>
      <Mouse>
        <ToothLeft />
        <ToothRight />
      </Mouse>
      <Blood />
    </>
  );
};
export default App;
