import React from 'react';
import styled, { keyframes } from 'styled-components';

const Animation = keyframes`
	50% {
  height: 1.35em;
	}
`;

const Normal = styled.div`
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 6.3em;
  height: 0.63em;
  border-radius: 4.5em;
  background: #111;
`;

const Teeth = styled.div`
  position: absolute;
  border-top: 0.8em solid white;
  border-left: 0.4em solid transparent;
  border-right: 0.4em solid transparent;
  left: 1.35em;

  &::before {
    position: absolute;
    border-top: 0.8em solid white;
    border-left: 0.4em solid transparent;
    border-right: 0.4em solid transparent;
    content: '';
    top: 0;
    left: 2.25em;
    transform: translateY(-100%);
  }
`;

const Saliva = styled.div`
  background-color: lightblue;
  height: 2.25em;
  width: 0.9em;
  border-bottom-left-radius: 4.5em;
  border-bottom-right-radius: 4.5em;
  z-index: 1;
  position: absolute;
  top: 48%;
  left: 10.8em;
  animation: ${Animation} 1.5s infinite;
`;

const App: React.FC = () => {
  return (
    <>
      <Normal>
        <Teeth />
      </Normal>
      <Saliva />
    </>
  );
};
export default App;
