import React, { useContext } from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  FlexDiv,
  LearnPageDiv,
  RightBtnDiv,
  MicroImg,
  LearnPageBtn,
  MicroCircle,
  Trapezoid,
  OXBtnDiv,
  BatteryDiv,
  TrashBtn,
  TrashDiv,
} from 'components/EtcStyles';
import BacteriaCombination from 'components/BacteriaCombination';
import AppContext from 'App/context';
import {
  PatternFirstType,
  generateBacteria,
  generateModelFirstLabel,
  modelFirst,
} from 'App/datas';
import RenderNoBacteria from 'components/RenderNoBacteria';
import Micro from 'assets/images/micro.png';
import { FaRegTrashAlt as Trash } from 'react-icons/fa';
import HelpBoxByGage from 'components/HelpBoxByGage';

const Page: React.FC = () => {
  const {
    state: { modelsFirst },
    setState,
    goToNext,
    learnBacteria,
    learnNoBacteria,
  } = useContext(AppContext);

  const gage =
    modelsFirst.bacteria.learnCount + modelsFirst.noBacteria.learnCount;
  const zeroPaddedGage = ['0', gage.toString()].join('').slice(-2);
  const param = generateModelFirstLabel();

  const handleChange = (learn: (_: keyof PatternFirstType) => void) => () => {
    if (gage < 50) {
      learn(param);
    }
  };
  const handleChangeO = handleChange(learnBacteria);
  const handleChangeX = handleChange(learnNoBacteria);

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI 1차 학습</ContentTitle>
          <ExplainBox>
            {`AI는 처음에 공기 중 물체가 세균인지, 아닌지 구분할 수 없어.
            우리가 AI에게 알려준다면 우리가 알려준대로 사물을 파악하게 될거야!
            이제 AI에게 세균을 알려주자!`}
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 5rem)' }}>
            <FlexDiv>
              <LearnPageDiv>
                <MicroImg src={Micro} alt="" />
                <Trapezoid />
                <MicroCircle>
                  <BatteryDiv>
                    <TrashDiv>
                      <TrashBtn
                        onClick={() =>
                          setState((prevState) => ({
                            ...prevState,
                            modelsFirst: {
                              bacteria: { ...modelFirst },
                              noBacteria: { ...modelFirst },
                            },
                          }))
                        }
                      >
                        <Trash />
                      </TrashBtn>
                      {zeroPaddedGage} / 50
                    </TrashDiv>
                  </BatteryDiv>
                  {param === 'bacteria' ? (
                    <BacteriaCombination type={generateBacteria()} />
                  ) : (
                    <RenderNoBacteria type={param} size={5} />
                  )}
                </MicroCircle>
                <OXBtnDiv>
                  <LearnPageBtn onClick={handleChangeO} color="#FF5667">
                    세균이다
                  </LearnPageBtn>
                  <LearnPageBtn onClick={handleChangeX} color="#5BC590">
                    세균아님
                  </LearnPageBtn>
                </OXBtnDiv>
                <RightBtnDiv>
                  <LearnPageBtn
                    onClick={() => {
                      goToNext();
                    }}
                    disabled={gage < 5}
                    color="#1696e5"
                    style={{ margin: 0 }}
                  >
                    학습완료
                  </LearnPageBtn>
                </RightBtnDiv>
              </LearnPageDiv>
            </FlexDiv>
            <HelpBoxByGage gage={gage} />
          </Content>
        </DivBase>
      </Container>
    </BasePage>
  );
};

export default Page;
