import React from 'react';
import styled from 'styled-components';

const Eyes = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  top: 2.25em;
  margin: auto;
  height: 0.45em;
  width: 6.3em;
  background: #383437;
  -moz-border-radius: 0.1em;
  -webkit-border-radius: 0.1em;
  border-radius: 0.1em;
  -moz-box-shadow: inset 2.5em 0 #2b272b;
  -webkit-box-shadow: inset 2.5em 0 #2b272b;
  box-shadow: inset 2.5em 0 #2b272b;

  &::before {
    content: '';
    position: absolute;
    top: 0.45em;
    height: 0.9em;
    width: 2.07em;
    background: #e8f0db;
    -moz-border-radius: 0 0 0.75em 0.75em;
    -webkit-border-radius: 0;
    border-radius: 0 0 0.75em 0.75em;
    -moz-box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    -webkit-box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    left: 0.4em;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0.45em;
    height: 0.9em;
    width: 2.07em;
    background: #e8f0db;
    -moz-border-radius: 0 0 0.75em 0.75em;
    -webkit-border-radius: 0;
    border-radius: 0 0 0.75em 0.75em;
    -moz-box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    -webkit-box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    box-shadow: 0 0.1em 0 0.1em #302d2f, 0 0.2em 0 0.2em #2b272b;
    right: 0.4em;
  }
`;

const App: React.FC = () => {
  return <Eyes />;
};
export default App;
