import React, { useContext } from 'react';
import styled from 'styled-components';
import { learningPage1Index, learningPage2Index } from 'App';
import AppContext from 'App/context';
import { modelFirst, modelSecond } from 'App/datas';

const Div = styled.div`
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  background: #ececec;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 32rem;
  z-index: 9;
  box-shadow: 5px 5px 7px #c4c4c4;
  font-size: 0.9rem;
`;
const BoldTitle = styled.div`
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 1rem;
`;
const RestartBtn = styled.button`
  width: 45%;
  border: 1px solid #999;
  border-radius: 0.7rem;
  background-color: #fff;
  cursor: pointer;
  font-family: 'Yspot';
  color: #717578;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  :last-child {
    margin-left: 3%;
  }
  :hover {
    color: #535353;
  }
`;

type Props = {
  goTo: string;
};

const PlayAgainBox: React.FC<Props> = ({ goTo }) => {
  const { setState } = useContext(AppContext);
  const handleClick = () => {
    setState((prev) => ({
      ...prev,
      page: goTo === 'first-step' ? learningPage1Index : learningPage2Index,
    }));
  };

  return (
    <Div>
      <BoldTitle>AI가 세균을 잘 구분 못하나요? </BoldTitle>
      AI는 내가 가르쳐준 내용을 그대로 학습해요. 학습 데이터를 <br />
      더 제공할 수록 ai가 더 많이 배웁니다. AI를 다시 학습시켜볼까요?
      <br />
      <RestartBtn onClick={handleClick}>다시 학습하기</RestartBtn>
    </Div>
  );
};

export default PlayAgainBox;
