import React from 'react';
import styled from 'styled-components';

const EyeRight = styled.div`
  position: absolute;
  top: 3.15em;
  left: 7em;
  width: 0.63em;
  height: 0.63em;
  background: #000;
  border-radius: 100%;
`;

const EyeLeft = styled(EyeRight)`
  left: 10em;
`;

const App: React.FC = () => {
  return (
    <>
      <EyeRight />
      <EyeLeft />
    </>
  );
};
export default App;
