import React from 'react';
import { Color, Body } from 'App/datas';
import Cloud from './Cloud';
import Diamond from './Diamond';
import Nail from './Nail';
import Round from './Round';
import Square from './Square';
import Star from './Star';

const colorMap = {
  [Color.GroupRed]: ['#ecbaba', '#b28383'],
  [Color.GroupYellow]: ['#f0e4a7', '#cdc89a'],
  [Color.GroupGreen]: ['#c8e1c2', '#adbda9'],
  [Color.GroupBlue]: ['#bfcbe7', '#a9b3c9'],
};

const colorRender = (color: number) => {
  const randomNum = Math.floor(Math.random() * 2);

  switch (color) {
    case Color.GroupRed:
      return colorMap[Color.GroupRed][randomNum];
    case Color.GroupYellow:
      return colorMap[Color.GroupYellow][randomNum];
    case Color.GroupGreen:
      return colorMap[Color.GroupGreen][randomNum];
    case Color.GroupBlue:
      return colorMap[Color.GroupBlue][randomNum];
    default:
      break;
  }
  return '';
};

const bodyMap = {
  [Body.Good]: (pickColor: string) => {
    switch (Math.floor(Math.random() * 2)) {
      case 0:
        return <Round color={pickColor} />;
      case 1:
        return <Cloud color={pickColor} />;
      default:
        break;
    }
    return <></>;
  },
  [Body.Soso]: (pickColor: string) => {
    switch (Math.floor(Math.random() * 2)) {
      case 0:
        return <Nail color={pickColor} />;
      case 1:
        return <Square color={pickColor} />;
      default:
        break;
    }
    return <></>;
  },
  [Body.Bad]: (pickColor: string) => {
    switch (Math.floor(Math.random() * 2)) {
      case 0:
        return <Star color={pickColor} />;
      case 1:
        return <Diamond color={pickColor} />;
      default:
        break;
    }
    return <></>;
  },
};

const Bodies: React.FC<{
  body: Body;
  color: Color;
}> = ({ body, color }) => {
  const pickColor = colorRender(color);
  return bodyMap[body](pickColor);
};

export default Bodies;
