import styled from 'styled-components';

const TongueSmile = styled.div`
  position: absolute;
  top: 2.25em;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 3em;
  height: 2.1em;
  background-color: black;
  border-radius: 70% 70% 3.5em 3.5em;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    width: inherit;
    height: 0.9em;
    background-color: tomato;
    border-radius: 50% 50% 0 0;
    bottom: 0;
    left: 0;
  }
`;

export default TongueSmile;
