import React from 'react';
import styled from 'styled-components';

const Normal = styled.div`
  position: absolute;
  width: 3.6em;
  height: 2.25em;
  background-color: black;
  border-radius: 70% 70% 3.5em 3.5em;
  overflow: hidden;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

const Teeth = styled.div`
  position: absolute;
  border-top: 0.72em solid #fffefb;
  border-left: 0.45em solid transparent;
  border-right: 0.45em solid transparent;
  left: 1.35em;

  &::before {
    position: absolute;
    border-top: 0.72em solid #fffefb;
    border-left: 0.45em solid transparent;
    border-right: 0.45em solid transparent;
    content: '';
    left: -1.1em;
    transform: translateY(-100%);
  }

  &::after {
    position: absolute;
    border-top: 0.72em solid #fffefb;
    border-left: 0.45em solid transparent;
    border-right: 0.45em solid transparent;
    content: '';
    left: 0.36em;
    transform: translateY(-100%);
  }
`;

const App: React.FC = () => {
  return (
    <>
      <Normal>
        <Teeth />
      </Normal>
    </>
  );
};
export default App;
