import React from 'react';
import styled from 'styled-components';

const Eye = styled.div`
  border: 0 solid transparent;
  border-top: 0.7em solid black;
  border-radius: 9em;
  width: 15%;
  height: 50%;
  transition: all 0.1s linear;
  position: absolute;
  top: 25%;
  left: 30%;

  &::before {
    position: absolute;
    content: '';
    height: 1.62em;
    width: 1.35em;
    top: 0.45em;
    border-radius: 100%;
    display: block;
    background-color: #000;
    left: 0.72em;
  }
`;
const Eye2 = styled(Eye)`
  left: 9.45em;
`;

const App: React.FC = () => {
  return (
    <>
      <Eye />
      <Eye2 />
    </>
  );
};
export default App;
