import styled from 'styled-components';
import { Content } from 'components/BaseStyles';

export const FlexDiv = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: relative;
`;

export const LearnPageDiv = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
`;

export const TrashDiv = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
export const TrashBtn = styled.button`
  background: none;
  padding: 0;
  border: 0;
  outline: 0;
  cursor: pointer;
  margin-bottom: 0.2rem;
  width: 1.4rem;
  height: 1.4rem;
  svg {
    fill: #bcbcbc;
    width: 100%;
    height: 100%;
    :hover {
      fill: #ececec;
    }
  }
`;

export const CustomBtn = styled.button`
  padding: 0 3rem;
  height: 3.5rem;
  color: #fff;
  background-color: #1696e5;
  outline: 0;
  border: 0;
  font-size: 2rem;
  font-family: 'Yspot', sans-serif;
  border-radius: 0.5rem;
  cursor: pointer;
`;

export const CustomBtnDiv = styled.div`
  position: absolute;
  bottom: 2rem;
`;

export const RightBtnDiv = styled(CustomBtnDiv)`
  bottom: 0rem;
  right: 5rem;
`;
export const RightBtn = styled(CustomBtn)`
  padding: 0.5rem 1.9rem;
  font-size: 1.2rem;
`;

export const Trapezoid = styled.div`
  position: absolute;
  right: 12rem;
  bottom: 8.5rem;
  width: 0;
  height: 0;
  border-top: 7rem solid transparent;
  border-left: 20rem solid #ececec;
  border-bottom: 7rem solid transparent;
  transform: rotate(15deg);
`;

export const CustomContent = styled(Content)`
  margin-top: 3rem;
  display: flex;
`;

export const RangeDiv = styled.div`
  width: 50%;
  border-radius: 2rem;
  margin: 0 1rem;
  :first-child {
    border: 0.8rem solid #22a415;
  }
  :last-child {
    border: 0.8rem solid #bf2d2d;
  }
`;
export const Title = styled.div`
  background-color: #22a415;
  color: #fff;
  font-size: 1.5rem;
  padding: 1rem;
  width: 8rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.8rem 0 0 0.8rem;
  border-radius: 1rem;
`;
export const Title2 = styled(Title)`
  background-color: #bf2d2d;
`;

export const MicroImg = styled.img`
  width: 17rem;
  position: absolute;
  bottom: 5rem;
  right: 1rem;
`;

export const LearnPageBtn = styled.button`
  background-color: ${(props) => props.color};
  height: 2.5rem;
  padding: 0.5rem 1.9rem;
  color: #fff;
  font-family: 'Yspot';
  border-radius: 4px;
  cursor: pointer;
  outline: 0;
  border: 0;
  font-size: 1.2rem;
  &:disabled {
    background: rgba(22, 150, 229, 0.4);
    cursor: not-allowed;
  }
  margin: 0 1rem;
`;

export const MicroCircle = styled.div`
  border-radius: 50%;
  border: 1px solid #bcbcbc;
  width: 30rem;
  height: 30rem;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  z-index: 1;
`;

export const OXBtnDiv = styled.div`
  margin-top: -2rem;
  z-index: 2;
`;

export const BatteryDiv = styled.div`
  position: absolute;
  top: 2rem;
  font-size: 1.5rem;
  font-weight: 800;
`;
