import React from 'react';
import styled from 'styled-components';

const Mouse = styled.div`
  z-index: 1;
  position: absolute;
  top: 0.9em;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 4.5em;
  height: 0.45em;
  background: #111;
`;

const TeethLeft = styled.div`
  position: absolute;
  top: 0.9em;
  left: 42.5%;
  width: 1.08em;
  height: 1.08em;
  background: #fff;
  border-radius: 0.27em;
  border: 0.2em solid #111;
`;
const TeethRight = styled(TeethLeft)`
  transform: translateX(1.62em);
`;

const App: React.FC = () => {
  return (
    <>
      <Mouse />
      <TeethLeft />
      <TeethRight />
    </>
  );
};
export default App;
