import React from 'react';
import styled, { keyframes } from 'styled-components';

const Blink = keyframes`
  100% {transform: rotateX(180deg);}
`;
const Eyebrows = keyframes`
  100% {top: 1.8em;}
`;

const Eyebrow1 = styled.div`
  width: 2.7em;
  height: 0.63em;
  border-radius: 0.9em;
  background-color: black;
  float: left;
  position: absolute;
  top: 1.35em;
  left: 5.4em;
  transform: rotate(25deg);
  animation: ${Eyebrows} 1s infinite alternate;
`;
const Eyebrow2 = styled.div`
  width: 2.7em;
  height: 0.63em;
  border-radius: 0.9em;
  background-color: black;
  margin-left: 1.5em;
  float: left;
  position: absolute;
  top: 1.35em;
  right: 5.4em;
  transform: rotate(-25deg);
  animation: ${Eyebrows} 1s infinite alternate;
`;
const Eye = styled.div`
  float: left;
  height: 2em;
  width: 1em;
  background: black;
  border-radius: 50%;
  margin-top: 2.7em;
  margin-left: 6.3em;
  animation: ${Blink} 1s cubic-bezier(1, 0.01, 1, -0.08) alternate infinite both;
  :last-child {
    margin-left: 3.15em;
  }
`;

const App: React.FC = () => {
  return (
    <>
      <Eyebrow1 />
      <Eyebrow2 />
      <Eye />
      <Eye />
    </>
  );
};
export default App;
