import React from 'react';
import styled, { keyframes } from 'styled-components';

const Wink = keyframes`
	15% {
  width: 1.35em;
  height: 0.18em;
  transform: rotate(2deg);
	}
`;
const WinkBrowRight = keyframes`
  15% {
  -webkit-transform: rotate(-10deg);
  transform: rotate(-10deg);
  }
`;
const WinkBrowLeft = keyframes`
  15% {
    top: 2.7em;
  }
`;
const EyebrowLeft = styled.div`
  position: absolute;
  top: 2.88em;
  width: 10%;
  height: 0.45em;
  border-radius: 0.9em;
  background: #000;
  transition: all 0.2s;
  left: 5.4em;
  animation: ${WinkBrowLeft} 1.2s infinite;
`;
const EyebrowRight = styled(EyebrowLeft)`
  left: 10.62em;
  animation: ${WinkBrowRight} 1.2s infinite;
`;
const EyeLeft = styled.div`
  position: absolute;
  top: 3.78em;
  left: 5.85em;
  border-radius: 100%;
  height: 1.08em;
  width: 1.08em;
  background-color: #111;
`;
const EyeRight = styled(EyeLeft)`
  left: 10.8em;
  animation: ${Wink} 1.2s infinite;
`;

const App: React.FC = () => {
  return (
    <>
      <EyebrowLeft />
      <EyeLeft />
      <EyebrowRight />
      <EyeRight />
    </>
  );
};
export default App;
