import React, { useContext } from 'react';
import AppContext from '../App/context';
import {
  Container,
  Title,
  SubTitle,
  BounceButton,
} from '../components/BaseStyles';

const Cover: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <Container>
      <Title>AI로봇 만들기</Title>
      <SubTitle>AI가 만들어지는 과정인 기계학습 이해하기</SubTitle>
      <BounceButton size="xxlarge" padding="large" onClick={goToNext}>
        시작하기
      </BounceButton>
    </Container>
  );
};

export default Cover;
