import ButtonGroup from 'components/ButtonGroup';
import ConveyerBelt from 'components/ConveyerBelt';

const Page: React.FC = () => (
  <BasePage>
    <Container>
      <DivBase>
        <ContentTitle>AI 성능검사</ContentTitle>
        <ExplainBox>
          {`학습 데이터를 활용해 AI가 유해균과 무해균을 인식하도록 훈련했어!
          이제 AI가 학습 내용에 따라 유해균을 파악해서 제거하기 시작해.`}
        </ExplainBox>
        <Content>
          <ConveyerBelt type="modelSecond" />
        </Content>
      </DivBase>
      <ButtonGroup />
    </Container>
  </BasePage>
);

export default Page;
