import React from 'react';
import styled from 'styled-components';

const Eye = styled.div`
  position: absolute;
  top: 2.7em;
  left: 5.85em;
  width: 1.62em;
  height: 1.62em;
  background: #fff;
  border-radius: 100%;
  border: 0.2em solid #333;

  &::before {
    position: absolute;
    content: '';
    top: -0.18em;
    left: 4.5em;
    width: 1.26em;
    height: 1.26em;
    background: #fff;
    border-radius: 100%;
    border: 0.2em solid #333;
  }
`;

const App: React.FC = () => {
  return <Eye />;
};
export default App;
