import React, { createContext } from 'react';
import {
  Bacteria,
  modelFirst,
  ModelFirstType,
  modelSecond,
  ModelSecondType,
  PatternFirstType,
} from './datas';

export type ModelsFirstType = {
  bacteria: ModelFirstType; // 세균
  noBacteria: ModelFirstType; // 세균아님
};

export type ModelsSecondType = {
  noxious: ModelSecondType; // 유해균
  innoxious: ModelSecondType; // 무해균
};

export type StateType = {
  page: number;
  bacterias: Bacteria[];
  modelsFirst: ModelsFirstType;
  modelsSecond: ModelsSecondType;
};

export type AppContextProps = {
  state: StateType;
  setState: React.Dispatch<React.SetStateAction<StateType>>;
  goToHome: () => void;
  goToPrev: () => void;
  goToNext: () => void;
  generateBacterias: (count: number) => void;
  learnBacteria: (param: keyof PatternFirstType) => void;
  learnNoBacteria: (param: keyof PatternFirstType) => void;
  learnNoxiousBacteria: (bacteria: Bacteria) => void;
  learnInnoxiousBacteria: (bacteria: Bacteria) => void;
  predictFirst: (param: keyof PatternFirstType) => keyof ModelsFirstType | null;
  predictSecond: (bacteria: Bacteria) => keyof ModelsSecondType | null;
};

export const minPage = 0;
export const defaultState: StateType = {
  page: minPage,
  bacterias: [],
  modelsFirst: {
    bacteria: { ...modelFirst },
    noBacteria: { ...modelFirst },
  },
  modelsSecond: {
    noxious: { ...modelSecond },
    innoxious: { ...modelSecond },
  },
};

const AppContext = createContext<AppContextProps>({
  state: defaultState,
  setState: () => {},
  goToHome: () => {},
  goToPrev: () => {},
  goToNext: () => {},
  generateBacterias: () => {},
  learnBacteria: () => {},
  learnNoBacteria: () => {},
  learnNoxiousBacteria: () => {},
  learnInnoxiousBacteria: () => {},
  predictFirst: () => null,
  predictSecond: () => null,
});

export default AppContext;
