import React from 'react';
import styled from 'styled-components';
import { Mouse, Eyes, Bacteria } from 'App/datas';
import { scaleUp } from 'styles/animations';

import SquintEye from 'components/bacteriaParts/eyes/Squint';
import EmptyEye from 'components/bacteriaParts/eyes/Empty';
import BeanEye from 'components/bacteriaParts/eyes/Bean';
import WinkEye from 'components/bacteriaParts/eyes/Wink';
import HeartEye from 'components/bacteriaParts/eyes/Heart';
import CatEye from 'components/bacteriaParts/eyes/Cat';
import FrankEye from 'components/bacteriaParts/eyes/Frank';
import AngryEye from 'components/bacteriaParts/eyes/Angry';
import SmileEye from 'components/bacteriaParts/eyes/Smile';

import NormalMouse from 'components/bacteriaParts/mouses/Normal';
import SmileMouse from 'components/bacteriaParts/mouses/Smile';
import SquareTeethMouse from 'components/bacteriaParts/mouses/SquareTeeth';
import TongueSmileMouse from 'components/bacteriaParts/mouses/TongueSmile';
import BloodMouse from 'components/bacteriaParts/mouses/Blood';
import PuppyMouse from 'components/bacteriaParts/mouses/Puppy';
import SalivaMouse from 'components/bacteriaParts/mouses/Saliva';
import SurprisedMouse from 'components/bacteriaParts/mouses/Surprised';
import ThreeTeethMouse from 'components/bacteriaParts/mouses/ThreeTeeth';
import Bodies from './bacteriaParts/bodies';

const eyesRender = (eyes: number) => {
  switch (eyes) {
    case Eyes.Good:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <HeartEye />;
        case 1:
          return <WinkEye />;
        case 2:
          return <SmileEye />;
        default:
          break;
      }
      return <></>;
    default:
      break;

    case Eyes.Soso:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <SquintEye />;
        case 1:
          return <EmptyEye />;
        case 2:
          return <BeanEye />;
        default:
          break;
      }
      return <></>;

    case Eyes.Bad:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <AngryEye />;
        case 1:
          return <FrankEye />;
        case 2:
          return <CatEye />;
        default:
          break;
      }
      return <></>;
  }
  return <></>;
};

const mouseRender = (mouse: number) => {
  switch (mouse) {
    case Mouse.Good:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <PuppyMouse />;
        case 1:
          return <TongueSmileMouse />;
        case 2:
          return <SmileMouse />;
        default:
          break;
      }
      return <></>;
    default:
      break;

    case Mouse.Soso:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <SquareTeethMouse />;
        case 1:
          return <SurprisedMouse />;
        case 2:
          return <NormalMouse />;
        default:
          break;
      }
      return <></>;

    case Mouse.Bad:
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <BloodMouse />;
        case 1:
          return <ThreeTeethMouse />;
        case 2:
          return <SalivaMouse />;
        default:
          break;
      }
      return <></>;
  }
  return <></>;
};
const BodyDiv = styled.div`
  position: relative;
  animation: ${scaleUp} 0.7s infinite alternate;
`;
const EyeDiv = styled.div`
  position: absolute;
  top: 15%;
  width: 100%;
  height: 27.5%;
`;
const MouseDiv = styled.div`
  width: 100%;
  height: 25%;
  position: absolute;
  top: 42.5%;
`;

type Props = {
  type: Bacteria | null;
  size?: number;
};

const BacteriaCombination: React.FC<Props> = ({ type, size = 19 }) => {
  if (!type) {
    return <div>-</div>;
  }
  const { body, eyes, mouse, color } = type;

  return (
    <BodyDiv
      style={{
        width: `${size}rem`,
        height: `${size}rem`,
        fontSize: `${size}px`,
      }}
    >
      <Bodies body={body} color={color} />
      <EyeDiv>{eyesRender(eyes)}</EyeDiv>
      <MouseDiv>{mouseRender(mouse)}</MouseDiv>
    </BodyDiv>
  );
};
export default React.memo(BacteriaCombination);
