import React from 'react';
import styled, { keyframes } from 'styled-components';

const PulseLeft = keyframes`
  30% {
    transform: scale(1.5, 1.5) rotate(-15deg);
  }
  70% {
    transform: scale(1, 1) rotate(-15deg);
  }
`;
const PulseRight = keyframes`
  30% {
    transform: scale(1.5, 1.5) rotate(15deg);
  }
  70% {
    transform: scale(1, 1) rotate(15deg);
  }
`;

const HeartEye = styled.div`
  position: absolute;
  top: 2.7em;
  left: 6.3em;
  transform: rotate(-15deg);
  animation: ${PulseLeft} 1s infinite;

  :last-child {
    left: auto;
    right: 6.3em;
    transform: rotate(15deg);
    animation: ${PulseRight} 1s infinite;
  }
`;
const Heart = styled.div`
  display: block;
  width: 1.15em;
  height: 1.15em;
  background: #e25d5d;
  transform: rotate(-45deg);

  &::before {
    top: -0.7em;
    left: 0;
    content: '';
    position: absolute;
    width: 1.15em;
    height: 1.15em;
    border-radius: 50%;
    background-color: #e25d5d;
  }
  &::after {
    left: 0.7em;
    top: 0;
    content: '';
    position: absolute;
    width: 1.15em;
    height: 1.15em;
    border-radius: 50%;
    background-color: #e25d5d;
  }
`;

const App: React.FC = () => {
  return (
    <>
      <HeartEye>
        <Heart />
      </HeartEye>
      <HeartEye>
        <Heart />
      </HeartEye>
    </>
  );
};
export default App;
