import React from 'react';
import styled from 'styled-components';

const Face = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 9em;
  height: 9em;

  &::before,
  &::after {
    position: absolute;
    content: '';

    top: 0.45em;
    left: 6.93em;
    z-index: 5;
    width: 1.62em;
    height: 1.26em;
    border-radius: 50% / 40%;
    border-right: 0.25em solid #000;
    border-bottom: 0.25em solid #000;
    border-left: 0.25em solid transparent;
    border-top: 0.25em solid transparent;
    box-shadow: 0px 0px #000;
  }
  &::after {
    left: 8.73em;
    transform: rotateY(180deg);
  }
`;

const Features = styled.div`
  position: absolute;
  content: '';

  &::after {
    position: absolute;
    content: '';
    left: 8.19em;
    top: -0.45em;
    width: 1.35em;
    height: 1.35em;
    background-color: #000;
    border-radius: 50%;
    box-shadow: 0.1em 0 #000, 0.05em 0.05em #000;
  }
`;

const App: React.FC = () => {
  return (
    <>
      <Face />
      <Features />
    </>
  );
};
export default App;
