import React from 'react';
import styled from 'styled-components';

const WhiteLeft = styled.div`
  position: absolute;
  top: 2.25em;
  left: 6.3em;
  background: #fff;
  width: 10%;
  height: 45%;
  border-radius: 100%;
`;
const WhiteRight = styled(WhiteLeft)`
  transform: translateX(3.5em);
`;

const BlackLeft = styled.div`
  position: absolute;
  top: 0.45em;
  left: 0.63em;
  background: #000;
  width: 1.08em;
  height: 1.26em;
  border-radius: 100%;
`;
const BlackRight = styled(BlackLeft)`
  transform: translateX(-0.56em);
`;

const App: React.FC = () => {
  return (
    <>
      <WhiteLeft>
        <BlackLeft />
      </WhiteLeft>
      <WhiteRight>
        <BlackRight />
      </WhiteRight>
    </>
  );
};
export default App;
