import React from 'react';
import styled from 'styled-components';
import Lottie from 'react-lottie';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';
import GuestList from 'assets/lotties/58305-guestlist.json';
import Code from 'assets/lotties/61918-code-animation.json';
import HelpBox from 'components/HelpBox';

const Point = styled.span`
  color: #1696e5;
`;

const WhatIsSupervisedLearning: React.FC = () => {
  const guestListOptions = {
    loop: true,
    autoplay: true,
    animationData: GuestList,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  };
  const codeOptions = {
    loop: true,
    autoplay: true,
    animationData: Code,
    rendererSettings: {
      preserveAspectRatio: 'xMidYMid slice',
    },
  };

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>지도 학습이란?</ContentTitle>
          <ExplainBox style={{ display: 'block' }}>
            <div>
              지금까지 입력값(Input data)인 이미지에 대한 결과값(Output
              data)으로 세균 여부를 학습시켜줬어.
            </div>
            <div>
              이처럼 <Point>원인과 결과에 대한 데이터 값을 입력</Point>시키는
              것을 <Point>지도학습</Point>이라 불러!
            </div>
          </ExplainBox>
          <Content style={{ display: 'flex', marginTop: '4rem' }}>
            <div style={{ width: '60%', height: '100%' }}>
              <Lottie options={guestListOptions} />
            </div>
            <div
              style={{
                width: '30%',
                height: '100%',
                display: 'flex',
                justifyContent: 'center',
                marginTop: '3rem',
                marginLeft: '-10rem',
              }}
            >
              <Lottie options={codeOptions} width={500} height={350} />
            </div>
          </Content>
          <HelpBox title="지도학습은 왜 하는 거야?">
            <div>AI의 핵심은 상황을 인식하고 예측하여, 판단을 내리는 거야!</div>
            <div>사용자가 이 콘텐츠를 좋아할지 안할지, 자동차라면</div>
            <div>우회전 할지 멈출지를 인공지능 모델에 의해 결정하지.</div>
          </HelpBox>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default WhatIsSupervisedLearning;
