import styled, { keyframes } from 'styled-components';

const Wink = keyframes`
  from { -webkit-transform: scale(0.8); }
`;

const Surprised = styled.div`
  position: absolute;
  background: #941e00;
  width: 1.8em;
  height: 2.25em;
  left: 45%;
  top: 0.45em;
  border-radius: 70% 25% 30%;
  animation: ${Wink} 0.5s infinite alternate;
`;

export default Surprised;
