import styled from 'styled-components';

const Round = styled.div`
  width: 85%;
  height: 85%;
  background-color: ${(props) => props.color};
  border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export default Round;
