import styled from 'styled-components';

const Star = styled.div`
  background: ${(props) => props.color};
  width: 70%;
  height: 70%;
  position: relative;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 13em;
    width: 13em;
    background: ${(props) => props.color};
  }

  &::before {
    transform: rotate(30deg);
  }

  &::after {
    transform: rotate(60deg);
  }
`;

export default Star;
