export enum Body {
  Good,
  Soso,
  Bad,
}

export enum Eyes {
  Good,
  Soso,
  Bad,
}

export enum Mouse {
  Good,
  Soso,
  Bad,
}

export enum Color {
  GroupRed,
  GroupYellow,
  GroupGreen,
  GroupBlue,
}

export type Bacteria = {
  eyes: Eyes;
  body: Body;
  mouse: Mouse;
  color: Color;
};

export const generateBacteria: () => Bacteria = () => ({
  eyes: Math.floor(Math.random() * 3),
  body: Math.floor(Math.random() * 3),
  mouse: Math.floor(Math.random() * 3),
  color: Math.floor(Math.random() * 4),
});

export type PatternFirstType = {
  bacteria: number;
  dust: number;
  hair: number;
  plant: number;
};
export type ModelFirstType = {
  learnCount: number;
  pattern: PatternFirstType;
};
export const modelFirst: ModelFirstType = {
  learnCount: 0,
  pattern: {
    bacteria: 0,
    dust: 0,
    hair: 0,
    plant: 0,
  },
};
export type NoBacteriaType = Omit<keyof PatternFirstType, 'bacteria'>;
export const generateNoBacteriasLabel: () => NoBacteriaType = () => {
  const noBacterias: NoBacteriaType[] = ['dust', 'hair', 'plant'];
  return noBacterias[Math.floor(Math.random() * noBacterias.length)];
};
export const generateModelFirstLabel: () => keyof PatternFirstType = () => {
  if (Math.random() > 0.5) {
    return 'bacteria' as keyof PatternFirstType;
  }
  return generateNoBacteriasLabel() as keyof PatternFirstType;
};

export type PatternSecondType = Record<keyof Bacteria, Record<number, number>>;
export type ModelSecondType = {
  learnCount: number;
  pattern: PatternSecondType;
};
export const modelSecond: ModelSecondType = {
  learnCount: 0,
  pattern: {
    eyes: {
      [Eyes.Good]: 0,
      [Eyes.Soso]: 0,
      [Eyes.Bad]: 0,
    },
    body: {
      [Body.Good]: 0,
      [Body.Soso]: 0,
      [Body.Bad]: 0,
    },
    mouse: {
      [Mouse.Good]: 0,
      [Mouse.Soso]: 0,
      [Mouse.Bad]: 0,
    },
    color: {
      [Color.GroupRed]: 0,
      [Color.GroupYellow]: 0,
      [Color.GroupGreen]: 0,
      [Color.GroupBlue]: 0,
    },
  },
};
