import styled from 'styled-components';

const Cloud = styled.div`
  background: ${(props) => props.color};
  width: 60%;
  height: 60%;
  margin-left: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &::before {
    border-radius: 50%;
    box-shadow: 10.5em 0 0 0 ${(props) => props.color},
      10.5em -2.5em 0 -0.2em ${(props) => props.color},
      10em -4.5em 0 -0.2em ${(props) => props.color},
      9.5em -7.5em 0 0.2em ${(props) => props.color},
      7em -9em 0 -0.2em ${(props) => props.color},
      4.5em -9em 0 0 ${(props) => props.color},
      1.5em -8em 0 0.3em ${(props) => props.color},
      0.5em -5.5em 0 -0.2em ${(props) => props.color},
      1em -3em 0 0.2em ${(props) => props.color},
      2em 1em 0 0.2em ${(props) => props.color},
      4.5em 1.5em 0 0.2em ${(props) => props.color},
      7em 2em 0 -0.1em ${(props) => props.color},
      9em 1.5em 0 -0.1em ${(props) => props.color};
    content: '';
    position: absolute;
    width: 35%;
    height: 35%;
    left: -15%;
    bottom: 0;
    background: ${(props) => props.color};
  }
`;

export default Cloud;
