import React from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import ButtonGroup from 'components/ButtonGroup';
import ConveyerBelt from 'components/ConveyerBelt';

const Page: React.FC = () => {
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI 성능검사</ContentTitle>
          <ExplainBox>
            {`이제 AI가 배운 내용에 따라 세균과 사물을 분석하고 구할 수 있어!
            과연 내가 만든 AI는 공기 중 세균을 잘 파악할까?`}
          </ExplainBox>
          <Content>
            <ConveyerBelt type="modelFirst" />
          </Content>
        </DivBase>
        <ButtonGroup />
      </Container>
    </BasePage>
  );
};

export default Page;
