import styled from 'styled-components';

const Nail = styled.div`
  width: 90%;
  height: 100%;
  background: ${(props) => props.color};
  border-radius: 100%;
  border-bottom-left-radius: 0.8em;
  border-bottom-right-radius: 0.8em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export default Nail;
