import React, { useContext } from 'react';
import AppContext from 'App/context';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
} from 'components/BaseStyles';
import {
  FlexDiv,
  RightBtnDiv,
  LearnPageDiv,
  MicroImg,
  MicroCircle,
  Trapezoid,
  BatteryDiv,
  LearnPageBtn,
  OXBtnDiv,
  TrashBtn,
  TrashDiv,
} from 'components/EtcStyles';
import Combination from 'components/BacteriaCombination';
import { Bacteria, generateBacteria, modelSecond } from 'App/datas';
import Micro from 'assets/images/micro.png';
import { FaRegTrashAlt as Trash } from 'react-icons/fa';
import HelpBoxByGage from 'components/HelpBoxByGage';

const Page: React.FC = () => {
  const {
    state: { modelsSecond },
    setState,
    goToNext,
    learnNoxiousBacteria,
    learnInnoxiousBacteria,
  } = useContext(AppContext);

  const gage =
    modelsSecond.noxious.learnCount + modelsSecond.innoxious.learnCount;
  const zeroPaddedGage = ['0', gage.toString()].join('').slice(-2);
  const bacteria = generateBacteria();

  const handleChange = (learn: (_: Bacteria) => void) => () => {
    if (gage < 50) {
      learn(bacteria);
    }
  };
  const handleChangeO = handleChange(learnNoxiousBacteria);
  const handleChangeX = handleChange(learnInnoxiousBacteria);

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>AI 2차 학습</ContentTitle>
          <ExplainBox>
            {`이처럼 AI는 네가 알려준 대로만 행동해.
            이제 네가 만든 AI는 세균인지 아닌지 알 수 있게되었어.
            추가로 AI에게 세균이 유해한지 아닌지를 파악하여 이불 속 유해균을 없앨 수 있도록 가르쳐보자.`}
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 5rem)' }}>
            <FlexDiv>
              <LearnPageDiv>
                <MicroImg src={Micro} alt="" />
                <Trapezoid />
                <MicroCircle>
                  <BatteryDiv>
                    <TrashDiv>
                      <TrashBtn
                        onClick={() =>
                          setState((prevState) => ({
                            ...prevState,
                            modelsSecond: {
                              noxious: { ...modelSecond },
                              innoxious: { ...modelSecond },
                            },
                          }))
                        }
                      >
                        <Trash />
                      </TrashBtn>
                      {zeroPaddedGage} / 50
                    </TrashDiv>
                  </BatteryDiv>
                  <Combination type={bacteria} />
                </MicroCircle>
                <OXBtnDiv>
                  <LearnPageBtn onClick={handleChangeO} color="#4AA5AC">
                    유해균
                  </LearnPageBtn>
                  <LearnPageBtn onClick={handleChangeX} color="#5BC590">
                    무해균
                  </LearnPageBtn>
                </OXBtnDiv>
                <RightBtnDiv>
                  <LearnPageBtn
                    onClick={goToNext}
                    disabled={gage < 5}
                    color="#1696e5"
                    style={{ margin: 0 }}
                  >
                    학습완료
                  </LearnPageBtn>
                </RightBtnDiv>
              </LearnPageDiv>
            </FlexDiv>
            <HelpBoxByGage gage={gage} />
          </Content>
        </DivBase>
      </Container>
    </BasePage>
  );
};

export default Page;
