import React, { useState } from 'react';
import { Bacteria, generateBacteria, PatternFirstType } from 'App/datas';
import Cover from 'pages/Cover';
import WhatIsAI from 'pages/WhatIsAI';
import HowToDecide from 'pages/HowToDecide';
import LearnByExperience from 'pages/LearnByExperience';
import AILearning1 from 'pages/AILearning1';
import AIPerformanceCheck1 from 'pages/AIPerformanceCheck1';
import WhatIsSupervisedLearning from 'pages/WhatIsSupervisedLearning';
import AIPerformanceResult1 from 'pages/AIPerformanceResult1';
import AILearning2 from 'pages/AILearning2';
import AIPerformanceCheck2 from 'pages/AIPerformanceCheck2';
import AIPerformanceResult2 from 'pages/AIPerformanceResult2';
import Finish from 'pages/Finish';
import Survey from 'pages/Survey';

import AppContext, {
  defaultState,
  minPage,
  ModelsFirstType,
  ModelsSecondType,
  StateType,
} from './context';

const pageComponents = [
  Cover,
  WhatIsAI,
  HowToDecide,
  LearnByExperience,
  AILearning1,
  AIPerformanceCheck1,
  WhatIsSupervisedLearning,
  AIPerformanceResult1,
  AILearning2,
  AIPerformanceCheck2,
  AIPerformanceResult2,
  Finish,
  ...(navigator.onLine ? [Survey] : []),
];

const pages = pageComponents.map((C) => <C />);

export const learningPage1Index = pageComponents.findIndex(
  (pageComponent) => pageComponent === AILearning1,
);
export const learningPage2Index = pageComponents.findIndex(
  (pageComponent) => pageComponent === AILearning2,
);

const maxStage = pages.length - 1;

const App: React.FC = () => {
  const [state, setState] = useState<StateType>(defaultState);
  const goToHome = () => setState(defaultState);
  const handleGoToPage = (addValue: number) => () => {
    setState((prevState) => {
      const newStage = prevState.page + addValue;
      const page =
        minPage <= newStage && newStage <= maxStage ? newStage : minPage;
      return {
        ...prevState,
        page,
      };
    });
  };
  const goToPrev = handleGoToPage(-1);
  const goToNext = handleGoToPage(1);
  const generateBacterias = (count: number) => {
    setState((prevState) => ({
      ...prevState,
      bacterias: Array.from(Array(count)).map(() => generateBacteria()),
    }));
  };

  const handleLearnFirst = (model: keyof ModelsFirstType) => (
    param: keyof PatternFirstType,
  ) => {
    setState((prevState) => ({
      ...prevState,
      modelsFirst: {
        ...prevState.modelsFirst,
        [model]: {
          ...prevState.modelsFirst[model],
          learnCount: prevState.modelsFirst[model].learnCount + 1,
          pattern: {
            ...prevState.modelsFirst[model].pattern,
            [param]: prevState.modelsFirst[model].pattern[param] + 1,
          },
        },
      },
    }));
  };
  const learnBacteria = handleLearnFirst('bacteria');
  const learnNoBacteria = handleLearnFirst('noBacteria');

  const handleLearnSecond = (model: keyof ModelsSecondType) => (
    bacteria: Bacteria,
  ) => {
    setState((prevState) => ({
      ...prevState,
      modelsSecond: {
        ...prevState.modelsSecond,
        [model]: {
          ...prevState.modelsSecond[model],
          learnCount: prevState.modelsSecond[model].learnCount + 1,
          pattern: {
            eyes: {
              ...prevState.modelsSecond[model].pattern.eyes,
              [bacteria.eyes]:
                prevState.modelsSecond[model].pattern.eyes[bacteria.eyes] + 1,
            },
            body: {
              ...prevState.modelsSecond[model].pattern.body,
              [bacteria.body]:
                prevState.modelsSecond[model].pattern.body[bacteria.body] + 1,
            },
            mouse: {
              ...prevState.modelsSecond[model].pattern.mouse,
              [bacteria.mouse]:
                prevState.modelsSecond[model].pattern.mouse[bacteria.mouse] + 1,
            },
            color: {
              ...prevState.modelsSecond[model].pattern.color,
              [bacteria.color]:
                prevState.modelsSecond[model].pattern.color[bacteria.color] + 1,
            },
          },
        },
      },
    }));
  };
  const learnNoxiousBacteria = handleLearnSecond('noxious');
  const learnInnoxiousBacteria = handleLearnSecond('innoxious');

  const predictFirst: (
    param: keyof PatternFirstType,
  ) => keyof ModelsFirstType | null = (param) => {
    const { bacteria, noBacteria } = state.modelsFirst;
    if (bacteria.learnCount + noBacteria.learnCount < 5) {
      return null;
    }
    const scoreBacteria = bacteria.pattern[param] / (bacteria.learnCount || 1);
    const scoreNoBacteria =
      noBacteria.pattern[param] / (noBacteria.learnCount || 1);
    if (scoreBacteria + scoreNoBacteria === 0) {
      return Math.random() > 0.5 ? 'bacteria' : 'noBacteria';
    }
    if (scoreBacteria >= scoreNoBacteria) return 'bacteria';
    return 'noBacteria';
  };

  const predictSecond: (bacteria: Bacteria) => keyof ModelsSecondType | null = (
    bacteria,
  ) => {
    const { noxious, innoxious } = state.modelsSecond;
    const { color, eyes, body, mouse } = bacteria;
    if (noxious.learnCount + innoxious.learnCount < 5) {
      return null;
    }
    const scoreNoxious =
      (noxious.pattern.color[color] +
        noxious.pattern.eyes[eyes] +
        noxious.pattern.body[body] +
        noxious.pattern.mouse[mouse]) /
      (noxious.learnCount || 1);
    const scoreInnoxious =
      (innoxious.pattern.color[color] +
        innoxious.pattern.eyes[eyes] +
        innoxious.pattern.body[body] +
        innoxious.pattern.mouse[mouse]) /
      (innoxious.learnCount || 1);
    if (scoreNoxious + scoreInnoxious === 0) {
      return Math.random() > 0.5 ? 'noxious' : 'innoxious';
    }
    if (scoreNoxious >= scoreInnoxious) return 'noxious';
    return 'innoxious';
  };

  return (
    <AppContext.Provider
      value={{
        state,
        setState,
        goToHome,
        goToPrev,
        goToNext,
        generateBacterias,
        learnBacteria,
        learnNoBacteria,
        learnNoxiousBacteria,
        learnInnoxiousBacteria,
        predictFirst,
        predictSecond,
      }}
    >
      {pages[state.page]}
    </AppContext.Provider>
  );
};

export default App;
