import React from 'react';
import styled from 'styled-components';

const Eyes = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;
const Eye = styled.div`
  background-color: #dddddd;
  border: 0.2em solid #071412;
  width: 2.7em;
  height: 2.16em;
  border-radius: 40% 120% 5% 120%;
  position: relative;
  overflow: hidden;
  float: left;
  &::after {
    content: '';
    position: absolute;
    background-color: #ffffff;
    bottom: 0;
    left: 0.45em;
    width: 1.8em;
    height: 2.25em;
    border-radius: 27% 100% 0% 100%;
    border-top: 1px solid rgba(177, 177, 177, 0.8);
  }
`;
const RightEye = styled(Eye)`
  transform: rotateY(180deg);
  margin-left: 1.8em;
`;
const Pupil = styled(Eye)`
  z-index: 2;
  margin-left: 1.08em;
  margin-top: 0.18em;
  width: 0.9em;
  height: 2.25em;
  background: #071412;
  border-radius: 100%;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    width: 0.225em;
    height: 0.225em;
    border-radius: 100%;
    top: 0.45em;
    left: 0;
    background: #ffffff;
    background: radial-gradient(
      center,
      ellipse cover,
      #ffffff 0%,
      #ffffff 42%,
      transparent 93%,
      transparent 100%
    );
  }
`;

const App: React.FC = () => {
  return (
    <Eyes>
      <Eye>
        <Pupil />
      </Eye>
      <RightEye>
        <Pupil />
      </RightEye>
    </Eyes>
  );
};
export default App;
