import styled from 'styled-components';

const Diamond = styled.div`
  width: 0;
  height: 0;
  border: 9em solid transparent;
  border-bottom: 9em solid ${(props) => props.color};
  position: relative;
  top: -9em;

  &::after {
    content: '';
    position: absolute;
    left: -9em;
    top: 9em;
    width: 0;
    height: 0;
    border: 9em solid transparent;
    border-top: 9em solid ${(props) => props.color};
  }
`;

export default Diamond;
