import React from 'react';
import HelpBox from 'components/HelpBox';

const HelpBoxByGage: React.FC<{ gage: number }> = ({ gage }) => {
  let content = '아직 세균을 제대로 구분할 수 없어....😖';
  if (gage >= 50) {
    content = `우리가 수집한 학습데이터는 여기까지야.\n끝까지 학습시켜줘서 고마워😍`;
  } else if (gage >= 25) {
    content = '더 많이 학습시킬수록 AI는 더 너를 닮아가!🙂';
  } else if (gage >= 20) {
    content = '고마워! 덕분에 많은 걸 배웠어!😎';
  } else if (gage >= 10) {
    content = `이제 조금은 알 것 같아.\n그래도 부족해😢`;
  } else if (gage >= 5) {
    content = '어떤 게 세균일까... 더 배우고 싶어!🧐';
  }

  return <HelpBox title="학습 도움말">{content}</HelpBox>;
};

export default HelpBoxByGage;
